@import '../../../styles/abstracts/variables';

.filter-panel .ant-list-split .ant-list-item {
  border-bottom: none !important;
}
.filter-actions {
  margin-top: 8px;
  font-size: 0.7rem;

  cursor: pointer;
  .filter-icon {
    color: var(--bg-gradient1);
  }
  .filter-label {
    color: #909090;
    font-weight: bold;
    margin-left: 4px;
  }
}

.filter-panel {
  background: var(--bg-filter-panel);
  min-height: 100px;
  margin-top: 8px;
  font-size: 0.6rem;

  .ant-list-item {
    font-size: 0.8em !important;
  }

  .ant-list-bordered {
    border: none !important;
  }

  .ant-list-items {
    height: 200px;
    overflow: auto;
    color: grey;
  }
  .ant-list-items::-webkit-scrollbar {
    width: 5px;
    background-color: #f5f5f5;
  }

  // Track
  .ant-list-items::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #f5f5f5;
  }

  // Handle
  .ant-list-items::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: grey;
    background-image: -webkit-gradient(
      linear,
      0 0,
      0 100%,
      color-stop(0.5, rgba(255, 255, 255, 0.2)),
      color-stop(0.5, transparent),
      to(transparent)
    );
  }

  // Handle on hover
  .ant-list-items::-webkit-scrollbar-thumb:hover {
    background: grey;
  }

  .ant-list-item {
    margin-right: 2px;
    &.selected {
      font-weight: 500 !important;
      color: #000 !important;
      font-size: 12px !important;
    }
  }

  //svg filter x icon
  .svg-icon {
    width: 1em;
    height: 1em;
    margin-left: 5px;
  }

  .svg-icon path,
  .svg-icon polygon,
  .svg-icon rect {
    fill: #000;
  }

  .svg-icon circle {
    stroke: #000;
    stroke-width: 1;
  }
}
